.alone__navbar {
    display:flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.alone__navbar-links {
    flex: 2;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.alone__navbar-links_logo {
    margin-right: 2rem;
}

.alone__navbar-links_logo h1 {
    font-size: 35px;
    color: #ff4820;
}


.alone__navbar-links_logo h1 span {
    font-size: 22px;
    color: #ffff;
}

.alone__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.alone__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.alone__navbar-links_container P,
.alone__navbar-sign P,
.alone__navbar-menu_container P {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.alone__navbar-sign button,
.alone__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #ff4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;   

}

.alone__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.alone__navbar-menu svg {
    cursor: pointer;
}

.alone__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0,0.2);
}

.alone__navbar-menu_container P {
    margin: 1rem 0;
}

.alone__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .alone__navbar-links_container {
        display: none;
    }

    .alone__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .alone__navbar {
       padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .alone__navbar {
       padding: 2rem;
    }

    .alone__navbar-sign {
        display: none;
    }

    .alone__navbar-menu_container {
        top: 20px;
    }

    .alone__navbar-menu_container-links-sign {
        display: block;
    }
}


    




