.alone__blog {
    display: flex;
    flex-direction: column; 
}

.alone__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.alone__blog-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 55px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.alone__blog-container {
    display: flex;
    flex-direction: row;
}

.alone__blog-container_groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.alone__blog-container_groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media  screen and (max-width: 990px) {
    .alone__blog-container {
        flex-direction: column-reverse;
    }

    .alone__blog-container_groupA {
        margin: 2rem 0;
    }

    .alone__blog-container_groupA .alone__blog-container_article {
        width: 48%;
    }

    .alone__blog-container_groupA .alone__blog-container_article-image {
        height: 250px;
    }

}

@media  screen and (max-width: 700px) {
   .alone__blog-container_groupB {
    grid-template-columns: repeat(1, 1fr);
   }

   .alone__blog-container_groupA .alone__blog-container_article {
    width: 100%;
   }

   .alone__blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
   }
}

@media  screen and (max-width: 700px) {
    .alone__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
       }
}

