.alone__header {
    display: flex;
}

.alone__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.alone__header-content h1 {
   font-family: var(--font-family);
   font-weight: 700;
   font-size: 55px;
   line-height: 60px;
   letter-spacing: -0.03em;
}

.alone__header-content p {
   font-family: var(--font-family);
   font-style: normal;
   font-weight: normal;
   font-size: 18px;
   line-height: 25px;
   color: var(--color-text);
   margin-top: 1rem;

}

.alone__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.alone__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}

.alone__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 19px;
    line-height: 27px;
    background-color: #ff4820;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}

.alone__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}

.alone__header-content__people img {
    width: 181.79px;
    height: 38px;
}

.alone__header-content__people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.alone__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.alone__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px){ 
    .alone__header {
        flex-direction: column;
    }

    .alone__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px){ 
    .alone__header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .alone__header p {
        font-size: 16px;
        line-height: 24px;
    }

    .alone__header-content__people {
        flex-direction: column;
    }

    .alone__header-content__people p {
       margin: 0;
    }

    .alone__header-content__input input,
    .alone__header-content__input button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .alone__header h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .alone__header p {
        font-size: 14px;
        line-height: 24px;
    }

    .alone__header-content__input input,
    .alone__header-content__input button {
        font-size: 12px;
        line-height: 16px;
    }
}