.chatGPT-app{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.logo {
   margin-right: 2rem;
}
.logo h1 {
    font-size: 35px;
    color: #ff4838;
}

.logo h1 span {
    font-size: 22px;
    color: #ffff;
}

footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 14px 10px;
    padding-right: 5px;
    gap: 10px;
    width: 100%;
    height: 50px;
    background: #2b2b36;
    border-radius: 10000px;
}

.composebar {
    padding: 0 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: #ffffff;
    outline: 0;
    border: 0;
    max-width: calc(100% - 120px);
    min-width: calc(100% - 120px);
    background-color: transparent;
}

.send-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 25px;
    height: 40px;
    background: linear-gradient(269deg, #2541fc 1.77%, #5466f2 99.15%);
    border-radius: 47px;
    cursor: pointer;
}

.send-button img {
    width: 20px;
    margin-right: -5px;
}

.chat-container {
    height: calc(100vh - 80px);
    padding: 20px 0;
}

.layout {
    width: 100%;
    height: 100%;
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
    scrollbar-width: none;
}

.chat-bubble {
    display: flex;
    gap: 15px;
}

.chat-bubble .avatar {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: #d9d9d9;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.chat-bubble.bot .avatar {
    background: linear-gradient(269deg, #2541fc 1.77%, #5466f2 99.15%);
}

.chat-bubble .avatar img {
    width: 22px;
}

.post {
    color: rgb(223, 212, 212);
    padding: 10px 20px;
    width: fit-content;
    max-width: 100%;
    min-height: 40px;
    background: #2b2b36;
    border-radius: 22px;
    white-space: pre-wrap;
}
.chat-bubble.bot .post {
    background: #21212c;
}
.loader {
    padding: 0 20px;
    width: fit-content;
    max-width: 100%;
    min-height: 40px;
    background: #2b2b36;
    border-radius: 22px;
    display: flex;
    align-items: center;
}



