body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.wrapper {
    position: relative;
    width: 400px;
    height: 500px;
    background: #000;
    box-shadow: 0 0 50px #ff4820;
    border-radius: 20px;
    padding: 40px;
}

.form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.form-wrapper h2 {
    font-size: 30px;
    color: #fff;
    text-align: center;
}

.input-group {
    position: relative;
    margin: 30px 0;
    border-bottom: 2px solid #fff;
}
.input-group label {
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    font-size: 16px;
    color: #fff;
    pointer-events: none;
    transition: .5s;
}

.input-group input {
    width: 320px;
    height: 40px;
    font-size: 16px;
    color: #fff;
    padding: 0 5px;
    background: transparent;
    border: none;
    outline: none;
}

.input-group input:focus~label,
.input-group input:valid~label {
    top: -5px;
}

.remember {
    margin: -5px 0 15px 5px;
}

.remember label {
    color: #fff;
    font-size: 14px;
}

.remember label input {
    margin-right: 5px;
    accent-color: #ff4820;
}

.form-wrapper button {
    position: relative;
    width: 100%;
    height: 40px;
    background: #ff4820;
    box-shadow: 0 0 10px  #ff4820;
    font-size: 16px;
    color: #000;
    font-weight: 500;
    cursor: pointer;
    border-radius: 30px;
    border: none;
    outline: none;
}

.signin-link,
.signup-link {
    font-size: 14px;
    text-align: center;
    margin: 15px 0;
}

.signin-link p,
.signup-link p {
    color: #fff;
}

.signin-link .signinBtn-link,
.signup-link .signUpBtn-link {
    color: #ff4820;
    text-decoration: none;
    font-weight: 500;
}

.signin-link .signinBtn-link:hover,
.signup-link .signUpBtn-link:hover {
    text-decoration: underline;
}